</template>

<script lang="uts">
	export default {
		data() {
			return {
				// ÁôªÂΩïÁõ∏ÂÖ≥Êï∞ÊçÆ
				isLoggedIn: false,
				loginAccount: '', // ËæìÂÖ•ÁöÑË¥¶Âè∑
				loginPassword: '', // ËæìÂÖ•ÁöÑÂØÜÁ†Å
				allowedAccount: 'admin', // ÊåáÂÆöË¥¶Âè∑
				allowedPassword: '123456', // ÊåáÂÆöÂØÜÁ†Å

				deviceId: '', // ËÆæÂ§áIDÔºàÊúÄÁªà‰ºöÊãºÊé•‰∏∫ "ÊâπÂè∑-ËÆæÂ§áÁºñÂè∑"Ôºâ
				currentProcessIndex: null, // ÂΩìÂâçÈÄâ‰∏≠ÁöÑÂ∑•Â∫èÁ¥¢Âºï
				processNames: [
					"ÔºàÂ∫∑ÈºéÔºâÁ©øÂÖâÁ∫§ÂèäÊ®°ÁªÑ",
					"ÔºàÂ∫∑ÈºéÔºâÂõ∫ÂÆöÁéØÂ§ÑÁêÜ",
					"ÔºàÂ∫∑ÈºéÔºâËøáÁ®ãÊ£Ä",
					"ÔºàÂ∫∑ÈºéÔºâÊàêÂìÅÊ£Ä",
					"Êù•ÊñôÊ£Ä",
					"Ê∏ÖÊ¥ó",
					"Âõ∫ÂÆöÂ§¥ÁÑäÊé•",
					"ÊèíÊé•Â§¥ÁÑäÊé•",
					"Êé•Âú∞Á∫øÁÑäÊé•",
					"ÁîµË∑ØÊùøÂ∞ÅËÉ∂",
					"‰∏äÂ£≥ÈîÅËΩÆÂÆâË£Ö",
					"Êï£ÁÉ≠ÁâáÂÆâË£Ö",
					"ÂØºÁÆ°ÂÆâË£Ö",
					"ËΩ¨ËΩÆÂÆâË£Ö",
					"‰∏äÂ£≥ÁªÑ‰ª∂ÂÆâË£Ö",
					"ÊâãÊüÑÊâãËΩÆÂÆâË£Ö",
					"ËøáÁ®ãÊ£Ä",
					"ÂÆåÂ∑•Ê£Ä",
					"ÂÜÖÂåÖÊ£Ä",
					"Â§ñÂåÖÊ£Ä",
					"ÊúÄÁªàÊ£Ä",
					"Âá∫ÂéÇÊ£Ä"
				],
				processList: Array(22).fill().map(() => ({
					inspector: '', // Ê£ÄÊµã‰∫∫Âëò
					inspectTime: '', // Ê£ÄÊµãÊó∂Èó¥
					image: '',      // Ê£ÄÊµãÂõæÁâá
					inspectResult: '' // Ê£ÄÈ™åÁªìÊûú
				})),
				inspectResultOptions: [
					"ÂêàÊ†º",
					"‰∏çÂêàÊ†º",
					"ÈÅÆÂÖâ3%ÂèØÈÅÆ",
					"ËΩØÂ§¥ÊºèÂÖâ",
					"ÂâçÁ´ØÂÖâÁ∫§Êñ≠",
					"ÈÄö‰ΩìÊºèÂÖâ",
					"ËΩØÂ§¥ÊºèÂÖâÈïøÂ∫¶Ë∂Ö5mm"
				],
				exportRequestActive: false,  // ÂØºÂá∫ËØ∑Ê±ÇÁä∂ÊÄÅÊ†áÂøó
				showDownloadTip: false  // Êñ∞Â¢ûÔºö‰∏ãËΩΩÊèêÁ§∫Ê†áÂøó
			}
		},
		computed: {
			currentProcess() {
				return this.currentProcessIndex !== null ? this.processList[this.currentProcessIndex] : null
			}
		},
		onLoad() {
			// ‰ªéÊú¨Âú∞Â≠òÂÇ®Ëé∑ÂèñÊâ´ÊèèÂéÜÂè≤
			const history = uni.getStorageSync('scanHistory')
			if (history) {
				this.scanHistory = JSON.parse(history)
			}
		},
		methods: {
			// Ê£ÄÊü•ÁôªÂΩïË¥¶Âè∑ÂíåÂØÜÁ†ÅÊòØÂê¶Ê≠£Á°Æ
			checkLogin() {
				if (
					this.loginAccount.trim() === this.allowedAccount &&
					this.loginPassword === this.allowedPassword
				) {
					this.isLoggedIn = true
				} else {
					uni.showToast({
						title: 'Ë¥¶Âè∑ÊàñÂØÜÁ†ÅÈîôËØØ',
						icon: 'none'
					})
				}
			},
			// Êâ´Á†ÅÂäüËÉΩ
			scanCode() {
				uni.scanCode({
					success: (res) => {
						// Áõ¥Êé•Â∞ÜÊâ´ÊèèÁªìÊûú‰Ωú‰∏∫ËÆæÂ§áÁºñÂè∑
						this.deviceId = res.result;
					},
					fail: (err) => {
						uni.showToast({
							title: 'Êâ´Á†ÅÂ§±Ë¥•',
							icon: 'none'
						});
					}
				});
			},
			
			// Â§ÑÁêÜÂ∑•Â∫èÈÄâÊã©
			handleProcessChange(e) {
				this.currentProcessIndex = Number(e.detail.value)
			},
			
			// Â§ÑÁêÜÊó•ÊúüÈÄâÊã©
			handleDateChange(e) {
				this.processList[this.currentProcessIndex].inspectTime = e.detail.value
			},

			// Â§ÑÁêÜÂõæÁâá‰∏ä‰º†
			handleUploadImage() {
				uni.chooseImage({
					count: 1,
					success: (res) => {
						const tempFilePath = res.tempFilePaths[0];
						// ÊçïËé∑ÂΩìÂâçÂ∑•Â∫èÁ¥¢Âºï
						const processIndex = this.currentProcessIndex;
						// ÊèêÂèñÊñá‰ª∂Êâ©Â±ïÂêçÔºåËã•Ê≤°ÊúâÂàôÈªòËÆ§‰ΩøÁî® .jpg
						const dotIndex = tempFilePath.lastIndexOf('.');
						const ext = dotIndex !== -1 ? tempFilePath.substring(dotIndex) : '.jpg';
						uni.showLoading({ title: '‰∏ä‰º†‰∏≠...' });
						// ÂØπËÆæÂ§áÁºñÂè∑ËøõË°å URL ÁºñÁ†ÅÔºåÁ°Æ‰øùÂÆâÂÖ®
						const validDeviceId = encodeURIComponent(this.deviceId);
						// ËøôÈáåÁõ¥Êé•Áî®ÁºñÂè∑‰Ωú‰∏∫Â∑•Â∫èÊ†áËØÜÔºàÂèØÊ†πÊçÆÈúÄË¶Å‰øÆÊîπ‰∏∫ÂÆûÈôÖÂ∑•Â∫èÂêçÁß∞Âπ∂ÁºñÁ†ÅÔºâ
						const validProcessName = 'P' + (processIndex + 1);
						// ÂØπÊ£ÄÊµã‰∫∫ÂëòÂíåÊ£ÄÊµãÊó∂Èó¥ÂÅö URL ÁºñÁ†Å
						const validInspector = encodeURIComponent(this.currentProcess.inspector);
						const validInspectTime = encodeURIComponent(this.currentProcess.inspectTime);
						// Êñ∞Â¢ûÔºöÂ∞ÜÊ£ÄÈ™åÁªìÊûúËΩ¨Êç¢‰∏∫Ëã±Êñá‰ª£Á†ÅÔºåÈÅøÂÖç‰∏≠ÊñáÂá∫Áé∞Âú®Êñá‰ª∂Âêç‰∏≠
						const inspectResultMapping = {
							"ÂêàÊ†º": "R1",
							"‰∏çÂêàÊ†º": "R2",
							"ÈÅÆÂÖâ3%ÂèØÈÅÆ": "R3",
							"ËΩØÂ§¥ÊºèÂÖâ": "R4",
							"ÂâçÁ´ØÂÖâÁ∫§Êñ≠": "R5",
							"ÈÄö‰ΩìÊºèÂÖâ": "R6",
							"ËΩØÂ§¥ÊºèÂÖâÈïøÂ∫¶Ë∂Ö5mm": "R7"
						};
						const validInspectResult = inspectResultMapping[this.currentProcess.inspectResult] || "UNK";
						// ÂÆö‰πâ‰∫ëÁ´ØÂ≠òÂÇ®Ë∑ØÂæÑÔºåÂΩ¢Â¶ÇÔºöupload/ËÆæÂ§áÁºñÂè∑_P1_ÊµãËØï‰∫∫Âëò_Ê£ÄÊµãÊó∂Èó¥_Ê£ÄÈ™åÁªìÊûú_Êó∂Èó¥Êà≥.ext
						const cloudPath = `upload/${validDeviceId}_${validProcessName}_${validInspector}_${validInspectTime}_${validInspectResult}_${Date.now()}${ext}`;
						uniCloud.uploadFile({
							filePath: tempFilePath,
							cloudPath: cloudPath
						}).then(async uploadRes => {
							if (uploadRes && uploadRes.fileID) {
								// Êñ∞Â¢ûÂõæÁâáÁ¥¢ÂºïËÆ∞ÂΩï
								await uniCloud.callFunction({
									name: 'submit_process',
									data: {
										type: 'IMAGE_INDEX',
										data: {
											deviceId: this.deviceId,
											processName: this.processNames[processIndex],
											fileID: uploadRes.fileID
										}
									}
								})
								
								uniCloud.getTempFileURL({
									fileList: [uploadRes.fileID]
								}).then(tempUrlRes => {
									let tempUrl = tempUrlRes.fileList[0].tempFileURL;
									// Ê∑ªÂä†Êó∂Èó¥Êà≥ÂèÇÊï∞Èò≤Ê≠¢ÁºìÂ≠òÈóÆÈ¢ò
									tempUrl = tempUrl + '?t=' + Date.now();
									// ‰ª•ÂìçÂ∫îÂºèÊñπÂºèÊõ¥Êñ∞ÂΩìÂâçÂ∑•Â∫èÂõæÁâá
									this.$set(this.processList, processIndex, {
										...this.processList[processIndex],
										image: tempUrl
									});
									uni.hideLoading();
									uni.showToast({ title: '‰∏ä‰º†ÊàêÂäü', icon: 'success' });
								}).catch(err => {
									uni.hideLoading();
									uni.showToast({ title: 'Ëé∑ÂèñÈ¢ÑËßàÂõæÂ§±Ë¥•', icon: 'none' });
									uni.__f__('error','at pages/index/index.vue:314',err);
								});
							} else {
								uni.hideLoading();
								uni.showToast({ title: 'ÂõæÁâá‰∏ä‰º†Â§±Ë¥•', icon: 'none' });
							}
						}).catch(err => {
							uni.hideLoading();
							uni.showToast({ title: 'ÂõæÁâá‰∏ä‰º†Â§±Ë¥•', icon: 'none' });
							uni.__f__('error','at pages/index/index.vue:323',err);
						});
					},
					fail: () => {
						uni.showToast({ title: 'ÂõæÁâáÈÄâÊã©Â§±Ë¥•', icon: 'none' });
					}
				});
			},

			// Êèê‰∫§Ë°®Âçï
			async handleSubmit() {
				if (!this.deviceId) {
					uni.showToast({
						title: 'ËØ∑ÂÖàËæìÂÖ•ÊàñÊâ´Á†ÅËé∑ÂèñËÆæÂ§áÁºñÂè∑',
						icon: 'none'
					})
					return
				}
				
				// Ë°®ÂçïÈ™åËØÅ
				if (!this.validateForm()) {
					return
				}

				try {
					uni.showLoading({
						title: 'Êèê‰∫§‰∏≠...'
					})
					
					const result = await uniCloud.callFunction({
						name: 'submit_process',
						data: {
							deviceId: this.deviceId,
							processName: this.processNames[this.currentProcessIndex],
							processData: {
								inspector: this.currentProcess.inspector,
								inspectTime: this.currentProcess.inspectTime,
								inspectResult: this.currentProcess.inspectResult,
								image: this.currentProcess.image
							}
						}
					})
					
					if(result.result.code === 0) {
						uni.showToast({
							title: 'Êèê‰∫§ÊàêÂäü',
							icon: 'success'
						})
						// ÈáçÁΩÆÂΩìÂâçÂ∑•Â∫èÊï∞ÊçÆ
						this.processList[this.currentProcessIndex] = {
							inspector: '',
							inspectTime: '',
							image: '',  // Ê≠§Â§Ñ‰øùÁïô image Â≠óÊÆµÁî®‰∫éÂêéÁª≠Êìç‰ΩúÔºàÂ¶ÇÈáçÊñ∞‰∏ä‰º†ÔºâÔºå‰ΩÜ‰∏ç‰ºöÊèê‰∫§
							inspectResult: ''
						}
					} else {
						throw new Error(result.result.msg)
					}
					
				} catch (error) {
					uni.showToast({
						title: error.message || 'Êèê‰∫§Â§±Ë¥•',
						icon: 'none'
					})
					uni.__f__('error','at pages/index/index.vue:387','Êèê‰∫§Â§±Ë¥•Ôºö', error)
				} finally {
					uni.hideLoading()
				}
			},

			// Êñ∞Â¢ûÔºöÂØºÂá∫ÂÖ®ÈÉ®Êï∞ÊçÆÊñπÊ≥ïÔºàË∞ÉÁî®‰∫ëÂáΩÊï∞ export_all_excelÔºâ
			async handleExport() {
				this.exportRequestActive = true;  // Ê†áËÆ∞ÂºÄÂßãÂØºÂá∫
				try {
					uni.showLoading({ title: 'ÂØºÂá∫‰∏≠...' });
					const res = await uniCloud.callFunction({ name: 'export_all_excel' });
					uni.hideLoading();
					// Â¶ÇÊûúÂú®Á≠âÂæÖËøáÁ®ã‰∏≠Áî®Êà∑ÁÇπÂáª‰∫ÜÁªàÊ≠¢
					if (!this.exportRequestActive) {
						uni.showToast({ title: 'ÂØºÂá∫Â∑≤ÁªàÊ≠¢', icon: 'none' });
						return;
					}
					if (res.result && res.result.code === 0) {
						uni.showToast({ title: 'ÂØºÂá∫ÊàêÂäü', icon: 'success' });
						uni.__f__('log','at pages/index/index.vue:407',"Excel Êñá‰ª∂ÈìæÊé•Ôºö", res.result.data.excelFile);
					} else {
						uni.showToast({ title: 'ÂØºÂá∫Â§±Ë¥•', icon: 'none' });
					}
				} catch (error) {
					uni.hideLoading();
					if (this.exportRequestActive) {
						uni.showToast({ title: 'ÂØºÂá∫Â§±Ë¥•', icon: 'none' });
					}
					uni.__f__('error','at pages/index/index.vue:416',error);
				}
				this.exportRequestActive = false; // ËØ∑Ê±ÇÁªìÊùü
			},

			// Êñ∞Â¢ûÔºöÁªàÊ≠¢ÂØºÂá∫Êìç‰ΩúÔºà‰ªÖÊéßÂà∂ÂâçÁ´ØÁä∂ÊÄÅÔºâ
			handleCancelExport() {
				if (this.exportRequestActive) {
					this.exportRequestActive = false;
					uni.hideLoading();
					uni.showToast({ title: 'Â∑≤ÁªàÊ≠¢ÂØºÂá∫', icon: 'none' });
					uni.__f__('log','at pages/index/index.vue:427','ÂØºÂá∫Êìç‰ΩúÂ∑≤ÁªàÊ≠¢');
				}
			},
			
			// Ë°®ÂçïÈ™åËØÅ
			validateForm() {
				const process = this.currentProcess;
				if (!process.inspector || !process.inspectTime) {
					uni.showToast({
						title: 'ËØ∑ÂÆåÂñÑÂΩìÂâçÂ∑•Â∫è‰ø°ÊÅØ',
						icon: 'none'
					});
					return false;
				}
				return true;
			},

			// Â§ÑÁêÜÊ£ÄÈ™åÁªìÊûúÈÄâÊã©
			handleInspectResultChange(e) {
				const index = Number(e.detail.value);
				this.processList[this.currentProcessIndex].inspectResult = this.inspectResultOptions[index];
			},

			// Êñ∞Â¢ûÔºöÊâπÈáè‰∏ãËΩΩÂäüËÉΩ
			async handleBatchDownload() {
				if (!this.deviceId) {
					uni.showToast({ title: 'ËØ∑ÂÖàËæìÂÖ•ËÆæÂ§áÁºñÂè∑', icon: 'none' });
					return;
				}
				try {
					uni.showLoading({ 
						title: 'Ê≠£Âú®ÁîüÊàêÂéãÁº©ÂåÖ...',
						mask: true
					});

					// ËÆæÁΩÆË∂ÖÊó∂ÁõëÊéßÔºà5ÂàÜÈíüÔºâ
					const timeout = setTimeout(() => {
						uni.showModal({
							title: 'ÊèêÁ§∫',
							content: 'Â§ÑÁêÜÊó∂Èó¥ËæÉÈïøÔºåËØ∑ÂãøÂÖ≥Èó≠È°µÈù¢',
							showCancel: false
						});
					}, 120000);

					const res = await uniCloud.callFunction({
						name: 'batch_download_images',
						data: { deviceId: this.deviceId }
					});

					if (!res.result.data?.zipUrl) {
						throw new Error('ÂéãÁº©ÂåÖÁîüÊàêÂ§±Ë¥•');
					}

					const tempUrlRes = await uniCloud.getTempFileURL({
						fileList: [res.result.data.zipUrl]
					});
					
					uni.downloadFile({
						url: tempUrlRes.fileList[0].tempFileURL,
						success: (downloadRes) => {
							if (downloadRes.statusCode !== 200) {
								throw new Error(`‰∏ãËΩΩÂ§±Ë¥•Ôºö${downloadRes.statusCode}`);
							}
							uni.saveFileToDisk({
								filePath: downloadRes.tempFilePath,
								success: () => {
									uni.showModal({
										title: '‰∏ãËΩΩÂÆåÊàê',
										content: 'ÂéãÁº©ÂåÖÂ∑≤‰øùÂ≠òËá≥ÊâãÊú∫‰∏ãËΩΩÁõÆÂΩï',
										showCancel: false
									});
								},
								fail: (e) => {
									uni.__f__('error','at pages/index/index.vue:500','‰øùÂ≠òÂ§±Ë¥•:', e);
									uni.showToast({ title: '‰øùÂ≠òÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•Â≠òÂÇ®ÊùÉÈôê', icon: 'none' });
								}
							});
						},
						fail: (err) => {
							throw new Error(`‰∏ãËΩΩÂ§±Ë¥•Ôºö${err.errMsg}`);
						}
					});
				} catch(e) {
					uni.__f__('error','at pages/index/index.vue:510','ÂØºÂá∫ÈîôËØØ:', e);
					uni.showToast({ 
						title: `Êìç‰ΩúÂ§±Ë¥•Ôºö${e.message || e.errMsg}`,
						icon: 'none',
						duration: 3000
					});
				} finally {
					clearTimeout(timeout);
					uni.hideLoading();
				}
			},

			openSettingGuide() {
				uni.showModal({
					title: 'ÊùÉÈôêÁî≥ËØ∑',
					content: 'ËØ∑Âú®Á≥ªÁªüËÆæÁΩÆ‰∏≠ÂÖÅËÆ∏Â∫îÁî®ËÆøÈóÆÁõ∏ÂÜåÊùÉÈôê',
					confirmText: 'ÂéªËÆæÁΩÆ',
					success: (res) => {
						if (res.confirm) {
							uni.openSetting();
						}
					}
				});
			},

			// Êñ∞Â¢ûÔºöË∑≥ËΩ¨ÂêéÂè∞
			goToAdmin() {
				uni.navigateTo({
					url: '/pages/admin/admin'
				})
			},
		}
	}
</script>

